<template>
  <div class="arounddetail mt-5">
    <div class="content p-5 mx-auto">
      <div class="text-left mt-5">
        <div class="title-text text-left">{{type}}</div>
        <div class="title-text text-left">{{title}}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'arounddetail',
  data () {
    return {
      title: '',
      type: ''
    }
  },
  created () {
    this.title = this.$route.query.id
    this.type = this.$route.query.type
  }
}
</script>

<style>

</style>



